import React, { useContext } from "react";
import { Link } from "react-router-dom";
import NewProfile from "../../images/profile/new-profile.png";
import { MouseContext } from "../context/mouseContext";
import { Helmet } from "react-helmet";
import { useTranslation } from "react-i18next";

const Home = () => {
  const { cursorChangeHandler } = useContext(MouseContext);
  const { t } = useTranslation();

  return (
      <React.Fragment>
        <Helmet>
          {/* SEO Page title tag */}
          <title>{t("meta.title")}</title>
          <meta name="description" content={t("meta.description")} />
        </Helmet>

        {/* Index-page Start */}
        <section id="home" className="section active">
          <div className="homecolor-box"></div>
          <div className="common_bg animate__animated animate__zoomIn">
            <div className="container m-auto">
              <div className="row align-items-center">
                {/* Profile-Pic */}
                <div className="col-xl-5 col-lg-6 col-md-6 col-12">
                  <div className="home-profile animate__animated animate__fadeInLeft animate__delay-1s">
                    <img src={NewProfile} alt="home-profile" />
                  </div>
                </div>

                {/* Profile-Information */}
                <div className="col-xl-7 col-lg-6 col-md-6 col-12">
                  <div className="home-content">
                    <p className="common-desctiption animate__animated animate__fadeInDown animate__delay-1s">
                      {t("home.greeting")}
                    </p>
                    <h1 className="common-title animate__animated animate__fadeInDown animate__delay-1s">
                      {t("home.name")}
                    </h1>
                    <div className="animated-bar animate__animated animate__fadeInDown animate__delay-2s"></div>
                    <div className="animated-text animate__animated animate__fadeInDown animate__delay-3s">
                      <h3>{t("home.role.backend")}</h3>
                      <h3>{t("home.role.web")}</h3>
                      <h3>{t("home.role.software")}</h3>
                    </div>

                    {/* Social media icons */}
                    <div className="fixed-block animate__animated animate__jackInTheBox animate__delay-2-5s">
                      <ul className="list-unstyled social-icons">
                        <li onMouseEnter={() => cursorChangeHandler("hovered")} onMouseLeave={() => cursorChangeHandler("")}>
                          <Link to="/"><i className="fab fa-twitter"></i></Link>
                        </li>
                        <li onMouseEnter={() => cursorChangeHandler("hovered")} onMouseLeave={() => cursorChangeHandler("")}>
                          <Link to="/"><i className="fab fa-facebook-square"></i></Link>
                        </li>
                        <li onMouseEnter={() => cursorChangeHandler("hovered")} onMouseLeave={() => cursorChangeHandler("")}>
                          <Link to="/"><i className="fab fa-linkedin"></i></Link>
                        </li>
                        <li onMouseEnter={() => cursorChangeHandler("hovered")} onMouseLeave={() => cursorChangeHandler("")}>
                          <Link to="/"><i className="fab fa-github-square"></i></Link>
                        </li>
                        <li onMouseEnter={() => cursorChangeHandler("hovered")} onMouseLeave={() => cursorChangeHandler("")}>
                          <Link to="/"><i className="fab fa-instagram-square"></i></Link>
                        </li>
                      </ul>
                    </div>

                    {/* Translated Description with Inline HTML */}
                    <p
                        className="lorem-text animate__animated animate__zoomIn animate__delay-2-5s"
                        dangerouslySetInnerHTML={{ __html: t("home.description") }}
                    ></p>

                    <div className="home-btn">
                      <Link
                          to="/contact"
                          className="clickbtn hire-me animate__animated animate__fadeInTopLeft animate__delay-3s"
                          onMouseEnter={() => cursorChangeHandler("hovered")}
                          onMouseLeave={() => cursorChangeHandler("")}
                      >
                        {t("home.buttons.hireMe")}
                      </Link>
                      <Link
                          to="/about"
                          className="clickbtn about-us animate__animated animate__fadeInTopRight animate__delay-3s"
                          onMouseEnter={() => cursorChangeHandler("hovered")}
                          onMouseLeave={() => cursorChangeHandler("")}
                      >
                        {t("home.buttons.aboutMe")}
                      </Link>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </React.Fragment>
  );
};

export default Home;
