import React, { useEffect, useState } from "react";
import { BrowserRouter } from "react-router-dom";
import Mavi from "./mavi";
import Cursor from "./mavi/components/common/cursor";
import SwitchWrapper from "./mavi/components/common/switchWrapper";
import MouseContextProvider from "./mavi/context/mouseContext";
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import { WOW } from "wowjs";
import "./index.css";

function App() {
  const [theme, setTheme] = useState("dark-theme");
  const [color, setColor] = useState("purple");
  const [cursor, setCursor] = useState("default-cursor");
  const [loading, setLoading] = useState(true);

  const wow = new WOW({
    boxClass: 'wow',      // default
    animateClass: 'animated', // default
    offset: 60,          // default
    mobile: false,       // default
    live: true           // default
  });

  // Имитация загрузки
  useEffect(() => {
    setTimeout(() => {
      setLoading(false);
    }, 2000);
  }, []);

  // Инициализация WOW.js
  useEffect(() => {
    wow.init();
  }, []);

  // Функция для обновления темы, цвета и курсора
  const updateTemplate = (newTheme, newColor, newCursor) => {
    setTheme(newTheme);
    setColor(newColor);
    setCursor(newCursor);
  };

  return (
      <React.Fragment>
        <MouseContextProvider>
          <BrowserRouter basename="/m-shchypailo">
            <div className={`${theme} ${color} ${cursor}`}>
              {/* Курсор */}
              <Cursor />
              {loading ? (
                  <React.Fragment>
                    {/* Прелоадер */}
                    <div id="preloader">
                      <div className="loader_line"></div>
                    </div>
                  </React.Fragment>
              ) : (
                  <React.Fragment>
                    {/* Переключатель тем, цветов и курсора */}
                    <SwitchWrapper template={updateTemplate} />

                    {/* Основной компонент */}
                    <Mavi />
                  </React.Fragment>
              )}
            </div>
          </BrowserRouter>
        </MouseContextProvider>
      </React.Fragment>
  );
}

export default App;
